import React, { Component } from 'react';
import ResourceManager from './ResourceManager';

/**
 * Loads appropriate content according to the current route
 */
class ContentLoader extends Component {
  constructor(props) {
    super(props);
    this.resourceManager = ResourceManager.getInstance();
    this.sites = this.resourceManager.getRawResource('contentloader.site.mapping.list').sites;
    let loading = this.resourceManager.getResource('contentloader', 'loadingmessage').text;
    let site = window.location.hash;
    if (site.startsWith('#')) {
      site = site.replace('#', '');
    }

    this.state = {
      content: (<p>{loading}</p>),
      location: site
    }

    this.updateContent = this.updateContent.bind(this);
  }
  
  componentDidMount() {
    window.onhashchange = this.updateContent;
    this.updateContent();
    /*
    $('iframe').load(function() {
        this.style.height =
        this.contentWindow.document.body.offsetHeight + 'px';
    });
    */
  }

  componentWillUnmount() {
    window.onhashchange = null;
  }

  updateContent() {
    let site = window.location.hash;
    if (site.startsWith('#')) {
      site = site.replace('#', '');
    }

    if (site === '') {
      site = 'home';
    }

    let siteId = this.sites.filter((item) => { return item.endsWith(`.${site}`); })[0];
    let siteMeta = this.resourceManager.getRawResource(siteId);
    if (siteMeta !== undefined && siteMeta.type === 'remote') {
      let size = window.innerHeight - 120;
      let height = `${size}px`;
      this.setState({
        content: (<iframe id='content-frame' title={siteMeta.title} src={siteMeta.site} height={height} width='100%' />),
        location: site
      });
    }
    else {
      this.setState(prevState => ({
        content: (<h2>Home Page [{site}] {(new Date()).toLocaleTimeString()}</h2>),
        location: site
      }));
    }
  }

  render () {
    //console.log(document.getElementById('content-frame').contentWindow.document.body.offsetHeight);
    return this.state.content;
  }
}

export default ContentLoader;



