import React, { Component } from 'react';
import MaterialIcon from './MaterialIcon';
import ResourceManager from './ResourceManager';

/**
 * Encapsulates Navigation bar brand from bootstrap
 */
class NavbarBrand extends Component {
  /*
  constructor(props) {
    super(props);
  }
  */

  generateBrandDef(elementDef) {
    if (elementDef.type === 'img') {
      return (
        <div>
          <img className='rounded-circle' alt={elementDef.alt} src={elementDef.source} /> {elementDef.alt}
        </div>
      );
    }

    if (elementDef.type === 'icon') {
      return (<MaterialIcon name={elementDef.name} />);
    }
  }

  render() {
    let rm = ResourceManager.getInstance();
    let brandDef = rm.getResource('navbarbrand', `${this.props.type}.brand`);
    let element = this.generateBrandDef(brandDef);

    return (
      <a className="navbar-brand" href={brandDef.href} >
        {element}
      </a>);
  }
}

export default NavbarBrand;

