import React, { Component } from 'react';
import ResourceManager from './ResourceManager';
import NavbarBrand  from './NavbarBrand';
import NavbarDropdown from './NavbarDropdown';
import NavbarToggle from './NavbarToggle';

/**
 * React component that describes the navigation bars
 */
class Navbar extends Component {
  /*
  constructor(props) {
    super(props);
  }
  */

  generateElements (location, alignment, unwrapped) {
    let rm = ResourceManager.getInstance();
    let elements = [];
    rm.getResources('navbar', `${location}.${alignment}`).forEach(el => {
      let label = el.label;
      if (el.appendYear === true) {
        label = `${label} ${(new Date()).getFullYear()}`
      }

      if (el.type === 'link') {
        let navLink = 'nav-link';
        if (el.disabled === true) {
          navLink = `${navLink} disabled`;
        }

        if (el.isCurrent === true) {
          let currentMsg = rm.getResource('navbar', 'link.current').label;
          if (unwrapped === true) {
            elements.push(
              <a key={el.id} className={navLink} href={el.href} >
                {label}
                <span className='sr-only' >{currentMsg}</span>
              </a>
            );
          }
          else {
            elements.push(
              <li key={el.id} className='nav-item active' >
                <a className={navLink} href={el.href} >
                  {label} 
                  <span className='sr-only' >{currentMsg}</span>
                </a>
              </li>
            );
          }
        }
        else {
          if (unwrapped === true) {
            elements.push(
              <a key={el.id} className={navLink} href={el.href} >{label}</a>
            );
          }
          else {
            elements.push(
              <li key={el.id} className='nav-item' >
                <a className={navLink} href={el.href} >{label}</a>
              </li>
            );
          }
        }
      }
      else if (el.type === 'dropdown') {
        elements.push(<NavbarDropdown key={el.id} id={el.id} unwrapped={unwrapped} />);
      }
    });

    return elements;
  }

  render() {
    let elementsLeft = [];
    let elementsRight = [];
    let contentLabel = '';
    let classes = 'navbar navbar-dark bg-dark container-fluid navbar-expand-lg ';

    if (this.props.type === 'bottom') {
      classes += 'fixed-bottom'
      contentLabel = 'navbarBottomContent';
      elementsLeft = this.generateElements(this.props.type, 'left', false);
    }

    if (this.props.type === 'top') {
      contentLabel = 'navbarTopContent';
      elementsLeft = this.generateElements(this.props.type, 'left', false);
      elementsRight = this.generateElements(this.props.type, 'right', true);
    }

    return (
    <nav className={classes}>
      <NavbarBrand type={this.props.type} />
      <NavbarToggle target={contentLabel} />
      <div className='collapse navbar-collapse' id={contentLabel} >
        <ul className='navbar-nav mr-auto' >{elementsLeft}</ul>
        {elementsRight}
      </div>
    </nav>
    );
  }
}

export default Navbar;

