import React, { Component } from 'react';
import ResourceManager from './ResourceManager';

class NavbarDropdown extends Component {
  render () {
    let rm = ResourceManager.getInstance();
    let definition = rm.getRawResource(this.props.id);
    let dropdownItems = [];
    definition.dropdownItems.forEach(element => {
      let item = rm.getRawResource(element);
      if (item.type === 'link') {
        dropdownItems.push(
          <a key={item.id} className='dropdown-item' href={item.href} >{item.label}</a>
        );
      }

      if (item.type === 'dropdownComponent') {
        dropdownItems.push(
          <div key={item.id} className='dropdown-divider' />
        );
      }
    });

    let element = (
      <li className='nav-item dropdown' >
        <a className='nav-link dropdown-toggle' 
          href={definition.href} 
          id={definition.target} 
          role='button' 
          data-toggle='dropdown'
          aria-haspopup='true'
          aria-expanded='false' >{definition.label}</a>
        <div className='dropdown-menu' aria-labelledby={definition.target} >
          {dropdownItems}
        </div>
      </li>
    );

    if (this.props.unwrapped === true) {
      element = (
        <div className='nav-item dropdown' >
          <a className='nav-link dropdown-toggle' 
            href={definition.href} 
            id={definition.target} 
            role='button' 
            data-toggle='dropdown'
            aria-haspopup='true'
            aria-expanded='false' >{definition.label}</a>
          <div className='dropdown-menu' aria-labelledby={definition.target} >
            {dropdownItems}
          </div>
        </div>
      );
    }

    return element;
  }
}

export default NavbarDropdown;



